class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
      aboutIsOpen: false,
      currentHover: 'index',
      isSafari: false
    }

    this.counter = 0;

    this.infiniteScroll = React.createRef();

    this.allCollections = [];

    this.handleScroll = this.handleScroll.bind(this);

    this.mouseEnterLink = this.mouseEnterLink.bind(this);

    this.dynamicLink = [true, true, true];

  }

  componentWillMount(){

    let collectionFetchData = function(data) {
      this.projectName = data.title.text;
      this.projectYear = data.year.text;
      this.projectTechnique = data.technique.text;
      this.images = data.image_gallery;
    }

    this.allCollections = [];

    this.props.data.allPrismicImagegallery.edges.forEach((item) => {
      
      this.allCollections.push(new collectionFetchData(item.node.data));

    });

    this.setState({
      collections: [...this.allCollections, ...this.allCollections, ...this.allCollections]
    });


  }

  componentDidMount() {

    this.container = this.infiniteScroll;

    TweenLite.to(this.backgroundImageRef1,5,{x: '-100%',scale: 2, ease:Power3.easeInOut})

    setTimeout(() => {
      TweenLite.to(document.querySelector('.spacer-top'),1,{height: '0',ease:Power3.easeInOut, 
      onComplete: () => {
        document.querySelector('.footer').style.visibility = "visible";
        document.querySelector('body').style.position = "absolute";
        this.backgroundImageRef.style.opacity = 1;
        this.backgroundImageRef1.style.display = "none";
      }
    });

    },2500);


    this.detectBrowser()
 

  //   setTimeout(() => {

  //     var text = new Blotter.Text("Hello", {
  //       family : "serif",
  //       size : 120,
  //       fill : "#171717"
  //     });
      
  //   var material = new Blotter.LiquidDistortMaterial();

  //   var blotter = new Blotter(material, { texts : text });

  //   var scope = blotter.forText(text);

  //   scope.appendTo(document.body);
  // },2500);

  // Wrap every letter in a span
document.querySelectorAll('.ml0,.ml1,.ml2').forEach(function(item){

  item.children[0].innerHTML = item.children[0].innerHTML.replace(/([^\x00-\x80]|\w)/g, "<span class='letter'>$&</span>");

});

document.querySelectorAll('.can-click').forEach((item)=>{
  item.addEventListener('mouseenter',this.mouseEnterLink);
  item.addEventListener('mouseleave',this.mouseLeaveLink);
})


  }

  detectBrowser() {

    var sBrowser, sUsrAg = navigator.userAgent;

    // The order matters here, and this may report false positives for unlisted browsers.
    
    if (sUsrAg.indexOf("Firefox") > -1) {
      sBrowser = "Mozilla Firefox";
      // "Mozilla/5.0 (X11; Ubuntu; Linux x86_64; rv:61.0) Gecko/20100101 Firefox/61.0"
    } else if (sUsrAg.indexOf("Opera") > -1 || sUsrAg.indexOf("OPR") > -1) {
      sBrowser = "Opera";
      //"Mozilla/5.0 (Macintosh; Intel Mac OS X 10_14_0) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/70.0.3538.102 Safari/537.36 OPR/57.0.3098.106"
    } else if (sUsrAg.indexOf("Trident") > -1) {
      sBrowser = "Microsoft Internet Explorer";
      // "Mozilla/5.0 (Windows NT 10.0; WOW64; Trident/7.0; .NET4.0C; .NET4.0E; Zoom 3.6.0; wbx 1.0.0; rv:11.0) like Gecko"
    } else if (sUsrAg.indexOf("Edge") > -1) {
      sBrowser = "Microsoft Edge";
      // "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/58.0.3029.110 Safari/537.36 Edge/16.16299"
    } else if (sUsrAg.indexOf("Chrome") > -1) {
      sBrowser = "Google Chrome or Chromium";
      // "Mozilla/5.0 (X11; Linux x86_64) AppleWebKit/537.36 (KHTML, like Gecko) Ubuntu Chromium/66.0.3359.181 Chrome/66.0.3359.181 Safari/537.36"
    } else if (sUsrAg.indexOf("Safari") > -1) {
      sBrowser = "Apple Safari";
      this.setState({
        isSafari: true
      });
      // "Mozilla/5.0 (iPhone; CPU iPhone OS 11_4 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/11.0 Mobile/15E148 Safari/604.1 980x1306"
    } else {
      sBrowser = "unknown";
    }
    
    // alert("You are using: " + sBrowser);    
  }


  handleScroll = () => {

    this.handleThrottledScroll();

}

handleAboutPageClick = () => {

  this.setState(prevState => ({
    aboutIsOpen: !prevState.aboutIsOpen
  }))

}

mouseEnterInfinitySlider = (e) => {

  this.setState({
    currentHover: e
  })



}

mouseLeaveInfinitySlider = () => {

  this.setState({
    currentHover: 'index'
  })

}

mouseEnterLink = (e) => {

  this.setState({
    currentHover: 'link'
  })

  if(e.target.children.length == 0) return;

  let number = e.target.children[0].classList.value.split('').splice(-1,1).join('');

  console.log(this.dynamicLink[number]);

  if(this.dynamicLink[number] != true) return;

  this.dynamicLink[number] = false;

  anime.timeline()
  .add({
    targets: `.ml${number} .letter`,
    translateY: [0,-100],
    translateZ: 0,
    opacity: [1,0],
    easing: "easeInExpo",
    duration: 350,
    delay: function(el, i) {
      return 300 + 30 * i;
    }
  }).add({
    targets: `.ml${number} .letter`,
    translateY: [100,0],
    opacity: [0,1],
    easing: "easeOutExpo",
    duration: 300,
    delay: function(el, i) {
      return 100 + 30 * i;
    },
    complete: () => this.dynamicLink[number] = true
  });

}

mouseLeaveLink = () => {
  this.setState({
    currentHover: 'index'
  })

}


  render(){

    return(
      <Transition>
          <Mouse currentHover={this.state.currentHover}/>

        <About className="can-click" onClick={this.handleAboutPageClick}>
          <img src={PlusButton}/>
        </About>

        <StyledAboutMinus className="can-click" pose={this.state.aboutIsOpen ? 'openAbout' : 'closedAbout'} onClick={this.handleAboutPageClick}>
          <img src={MinusButton}/>
      </StyledAboutMinus>

      <SpacerTop className="spacer-top"/>
      <ContainerTop className='main-background'>   
      <BackgroundImage ref={div => this.backgroundImageRef1 = div} src={Logo}/>
      <BackgroundImage2 ref={div => this.backgroundImageRef = div} src={Logo}/> 
      <Footer className={'footer'}>
        <span className="can-click-parent">Anna Genger / website by <span className="can-click"><span className="ml0"><a href="https://karlanders.io" target="_blank">Studio Karl Anders</a></span></span></span>
        <Socials>
        <span className="can-click"><span className="ml1"><a href="https://www.instagram.com/annagenger/" target="_blank">instagram</a></span></span>
        <span className="can-click"><span className="ml2"><a href="https://www.instagram.com/annagenger/" target="_blank">facebook</a></span></span>
        </Socials>
      </Footer>
      </ContainerTop>
      <Drawer style={{overflow: 'hidden'}} pose={this.state.aboutIsOpen ? 'openIndex' : 'closedIndex'}> 
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      {/* <CornerSnipTop/> */}
      <Container>
      <ProjectDiv className={'infinite-scroll'} ref={this.infiniteScroll} onScroll={this.handleScroll}>
        {
          this.state.collections.map((item, index) => {
            return (
            <InfiniteSlider isSafari={this.state.isSafari} mouseLeave={this.mouseLeaveInfinitySlider} mouseEnter={this.mouseEnterInfinitySlider} key={index} backgroundColor='white' number={index} data={item}/>
              )
          })
        }
      </ProjectDiv>
      </Container>
      {/* <CornerSnipBottom/> */}
      </Drawer>
      {/* <Spacer/> */}
      <StyledOverlay onClick={this.handleAboutPageClick} pose={this.state.aboutIsOpen ? 'open' : 'closed'}/>
      <AboutPage open={this.state.aboutIsOpen}/>
      </Transition>
    )
  }
}

export default IndexPage

